import React, { Component } from 'react';
import { Route, Switch, Router } from 'react-router-dom'
import MenuAppBar from './components/Header'
import Home from './components/Home'
// import LoginTab from './components/signin'
// import ImageArea from './components/picArea';
// import Search from './components/Search'
import { Provider } from 'react-redux';
import store from './store/store'
import Createads from './components/CreateAds'
import SignIn from './components/Sign-in';
import SignUp from './components/Sign-up';
import Addetail from './components/Addetail'
import Search from './components/Search';
import Footer from './components/Footer'
import Dashboard from './components/dashboard'
import {setUser} from './store/Actions/Action_SetUser';
import history from './history'


class App extends Component {

  constructor() {
    super();
    fetch('/is_authenticated', {

    }).then((res) => {
      return res.json();
    }).then((resp) => {
      if (resp._id) {
        // history.push('/post-ads')
        store.dispatch(setUser(resp))
        console.log(resp)
      }
    }).catch((err) => console.log(err))
  }


  render() {
    return (
      <Provider store={store}>
        <Router history={history}>
          <div >
            <MenuAppBar />
            <div style={{ minHeight: 530 }}>



              <Search />



              <Switch>

                <Route exact path='/' component={Home} />
                <Route path='/SignIn' component={SignIn} />
                <Route path='/post-ads' component={Createads} />
                <Route path='/Sign-up' component={SignUp} />
                <Route exact path={"/userDetails"} component={Dashboard} />
              </Switch>
              <Route exact path='/userDetails/:_id' component={Addetail} />



            </div>
            <Footer />

          </div>

        </Router>
      </Provider>
    );
  }
}

export default App;
