import {createStore,combineReducers} from 'redux';
import Reducer_ads from './Reducers/reducer_ads';
import Reducer_User from './Reducers/reducer_User';
import Reducer_addetails from'./Reducers/reducer_addetails'
// import Reducer_searches from './Reducers/reducer_searches'
import Reducer_useradd from './Reducers/reducer_useradd'

const allReducers=combineReducers({Reducer_ads,Reducer_User,Reducer_addetails,Reducer_useradd})
let store= createStore(allReducers);


window.store=store
export default store