import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import AccountCircle from '@material-ui/icons/AccountCircle';


import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import Search from '@material-ui/icons/Search'
import { Button, IconButton, AppBar, Toolbar, Typography } from '@material-ui/core'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import classNames from 'classnames';
import {Redirect} from 'react-router-dom'
import {logoutUser} from '../store/Actions/Action_SetUser'


const styles = {
  root: {
    flexGrow: 1,
    marginBottom:'10px'
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  logo:{
    underlineStyle: 'none',
    color: 'black'
  }
};

class MenuAppBar extends React.Component {


  constructor(){
    super()
    fetch('/is_authenticated',{
      
    }).then((res)=>{
      return res.json();
    }).then((resp)=>{
      if(resp._id){
        this.setState( {
          auth:true,
        })
      }
      else{
      this.setState(  {
        auth : false,

      }   )
      }
    }).catch((err)=>console.log(err))
  }
  state = {
    auth: false,
    anchorEl: null,
  };

  logout = ()=>{
    fetch('/logout',{

    }).then((res)=>{
      return res.json()
    }).then((resp)=>{
     
      if (resp !== undefined) {
      alert(resp); 
      this.props.dispatch(logoutUser()); 
      
     
    }
    })
  }
  // handleChange = event => {
  //   this.setState({ auth: event.target.checked });
  // };

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };





  render() {
   

    const { classes } = this.props;
    const { anchorEl } = this.state;
    const open = Boolean(anchorEl);
    const { user } = this.props






    return (

      <div className={classes.root}>


        <AppBar position="static" style={{ backgroundColor: 'white' }}>
          <Toolbar>
            {/* <IconButton className={classes.menuButton} aria-label="Menu">
              <MenuIcon />
            </IconButton> */}
            <Typography variant="h6" className={classNames(classes.grow,classes.logo)}>
              <Link to='/' style = {{textDecoration:'none',color:'black'}}>AlphaShop</Link>
            </Typography>

            <Link to='/search'><IconButton >
              <Search />

            </IconButton> </Link>

            (
              <div>
              <IconButton
                aria-owns={open ? 'menu-appbar' : undefined}
                aria-haspopup="true"
                onClick={this.handleMenu}
                variant="contained"

              >

                {user.username ? user.fullname : <AccountCircle />}
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={this.handleClose}
              >
               
               {user.username ? <Link to={'/userDetails'}><MenuItem onClick={this.handleClose}>My account</MenuItem></Link>:null}
                <MenuItem>{user.username ? <Button onClick = {this.logout}>logout</Button> : <Link to='/Signin'> Login</Link>}</MenuItem>
              </Menu>

            </div>

            <Link to='/post-ads'><Button >POSTADS</Button></Link>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}
const mapStateToProps = (store) => {
  return {
    user: store.Reducer_User
  }
}


MenuAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(mapStateToProps)(withStyles(styles)(MenuAppBar));