import React, { Component } from 'react'
import { connect } from 'react-redux'
import '../css/Header.css'
import useradd from '../store/Actions/Action_Useradds';
const style = {
    upperbox: {
        textAlign: 'center'
    },
    name: {
        fontSize: '40px',
        fontWeight: '400',
        fontFamily: 'Comic Sans MS, Comic Sans, cursive'
    },
    adsArea: {
        margin: '20px',
        padding: '20px',

    }

}



class Dashboard extends Component {
    componentDidMount = () => {
        var id = this.props.user._id;
        var options = {
            method: 'POST',
            body: JSON.stringify({ id }),
            headers: {
                'Content-Type': 'application/json'
            }
        }


        fetch('/userads', options)
            .then(res => res.json())
            .then(json => {
                this.props.dispatch(useradd(json))
                console.log(json)
            })

    }




    render() {
        const { user } = this.props
        const { files } = this.props

        var addCard;
        if (files !== undefined) {
            addCard = files.map((ad, index) => {
                return <div className="responsive" key={index}>
                    <div className="gallery">

                        <img src={ad.files[0].path} alt='pic' />

                        <div className="desc"><h5>{ad.purpose}</h5>
                            <b>Location:</b> {ad.location}<br />
                            <b>Area:</b> {ad.area + ' ' + ad.unit}<br />
                            <b>Price:</b> {ad.price}<br />
                            Recomendation by owner <br />



                        </div>
                    </div>

                </div>
            })
        }
        return (
            <div>
                <div style={style.upperbox}>
                    <div style={style.name}>
                        <p>{user.fullname}</p>
                    </div>
                    <h4>{user.email}</h4>
                </div><hr />
                <div style={style.adsArea}>
                    <h4>Published ads:</h4>
                    <div>
                        {addCard}
                    </div>

                </div>
            </div>
        )
    }
}
const mapStateToProps = (store) => {
    return (
        {
            user: store.Reducer_User,
            files: store.Reducer_useradd
        }
    )
}

export default connect(mapStateToProps)(Dashboard)