import React from 'react';
import { Fade } from 'react-slideshow-image';
import imag from '../images/one.jpg'
import imag2 from '../images/two.jpg'
import imag3 from '../images/three.jpg'
import '../css/image.css'
 
const fadeImages=[
  imag,imag2,imag3
]
 
const fadeProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: false
}
 
const ImageArea = () => {
  return (
      <div className='hidediv'>
    <Fade {...fadeProperties}>
      <div className="each-fade">
        <div className="image-container">
          <img src={fadeImages[0]} alt='pic' height='400px' width='100%'/>
        </div>
       
      </div>
      <div className="each-fade">
        <div className="image-container">
        <img src={fadeImages[1]} alt='pic' height='400px' width='100%'/>
        </div>
       
      </div>
      <div className="each-fade">
        <div className="image-container">
        <img src={fadeImages[2]} alt='pic' height='400px' width='100%'/>
        </div>
      
      </div>
      
    
    </Fade>
    </div>
  )
}
export default ImageArea