import React, { Component } from 'react';
import { FormHelperText, } from '@material-ui/core'
import addsearches from '../store/Actions/Action_Searches'
import { addpost } from '../store/Actions/Action_Addads'

import { connect } from 'react-redux'
import '../css/main.css'

// import { Redirect } from 'react-router-dom'
import Home from './Home';


class Searchpanel extends Component {


    state = {
        city: '',
        prefer: '',
        auth: false
    }
    handleChange = name => event => {
        this.setState({ [name]: event.target.value })


    }
    handleSubmit = (e) => {
        e.preventDefault();
        let city = this.state.city
        let a = this.state.prefer
        let prefer = a.toLowerCase();
        let search = { city, prefer }
        console.log(search)
        var options = {
            method: 'POST',
            body: JSON.stringify(search),
            headers: {
                'Content-Type': 'application/json'
            }
        }


        fetch('/searching', options)
            .then(res => res.json())
            .then(json => {
                console.log(json);
                this.props.dispatch(addpost(json))
                if (json.length === 0) {
                    this.setState({ auth: true })
                } else {
                    this.setState({ auth: false })
                }
            })


            .catch(error => console.error('Error:', error));


    }



    render() {
        const { auth } = this.state

        return (<div>
            <div className="s003">
                <form>
                    <div className="inner-form">
                        <div className="input-field first-wrap">
                            <div className="input-select">
                                <select value={this.state.city} onChange={this.handleChange('city')} style={{ width: '100%', height: '65px' }} name="choices-single-defaul">
                                    <option placeholder="">City</option>
                                    <option value="Faisalabad">Faisalabad</option>
                                    <option value="Lahore">Lahore</option>
                                    <option value='Islamabad'>Islamabad</option>
                                    <option value='Karachi'>Karachi</option>

                                </select>
                            </div>
                        </div>

                        <div className="input-field second-wrap">
                            <input id="search" type="text" name='prefer' value={this.state.prefer} onChange={this.handleChange('prefer')} placeholder="e.g coffee shop" />
                            <FormHelperText><b>Your preferred Business Description</b></FormHelperText>
                        </div>
                        <div className="input-field third-wrap">
                            <button className="btn-search" onClick={this.handleSubmit} type="button">
                                Search
                            </button>
                        </div>
                    </div>
                </form>

            </div>

            <h3>RECOMMENDATIONS</h3>
            {auth && (
                <p>no result Found</p>
            )}

            <Home />
        </div>
        )
    }
}
export default connect()(Searchpanel);