import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'

import '../css/Header.css';

const styles = {
    card: {
        maxWidth: 325

    },
    media: {
        objectFit: 'cover',
    },
    rec:{
        border:'solid lightgrey 1px',
        borderRadius:'20px',
        backgroundColor: 'lightgrey'
    }
};




class AddCards extends Component {

  
   




    render() {
       
        const { ads } = this.props
        const addCard = ads.map((ad, index) => {
        
            return <div className="responsive" key={ad._id}>
              <a href={'/userDetails/'+ad._id} style = {{textDecoration:'none',color:'black'}}><div className="gallery">

              <img src={ad.files[0].path} alt='images' height="50" />
              {/* <CardMedia
                image = {ad.files[0].path}  
            /> */}

                    <div className="desc"><h5>{ad.purpose}</h5>
                        <b>Location:</b> {ad.location}<br />
                        <b>Area:</b> {ad.area + ' ' + ad.unit}<br />
                        <b>Price:</b> {ad.price}<br/>
                        Recomendation by owner <br/>
                        {ad.recommendation.map((ab)=>{
                       return <span style={styles.rec}>{ab+','}</span>})}
                   

                    </div>
                     </div>
                     </a>            
            </div>




        })
        return (

            <div>
                {addCard}
            </div>



        );
    }
}



const mapStateToProps = (store) => {
    return {
        ads: store.Reducer_ads
    }
}

export default connect(mapStateToProps)(withStyles(styles)(AddCards));