import React, { Component } from 'react';
import { connect } from 'react-redux'
import { setadd } from '../store/Actions/Action_Addads'
import { Fade } from 'react-slideshow-image';





const fadeProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: false
}



const style = {
  main: {
    display: 'flex',
    flexDirection: 'row'

  },
  images: {
    flexGrow: 3,
    width: '55%',
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    height: '500px',
    margin: '5px',

  },
  detail: {
    flexGrow: 2,
    width: '35%',




  },
  priceDetail: {
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    margin: '5px',
    padding: '5px',

  },
  userDetail: {
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    margin: '5px',
    padding: '5px',
    textAlign: 'center',

  },
  addDetails: {
    border: '1px',
    borderColor: 'lightgrey',
    borderStyle: 'solid',
    margin: '5px',
    padding: '5px',

  }
}

class Addetail extends Component {
  componentDidMount() {
    var id = this.props.match.params._id;
    var _id = id.toString()

    var options = {
      method: 'POST',
      body: JSON.stringify({ _id }),
      headers: {
        'Content-Type': 'application/json'
      }
    }


    fetch('/addetail', options)
      .then(res => res.json())
      .then(json => {
        // this.setState(json);
        // console.log(json)
        this.props.dispatch(setadd(json))


      }


      )
      .catch((error) => { console.log(error) });
  }



  render() {
    const { file } = this.props;
    console.log(file.files)
    let data;
    if (file.recommendation !== undefined) {
      data = file.recommendation.map((el, i) => {
        return <div key={i}>{el}</div>
      })

    }
    let imag;
    if (file.files !== undefined ) {
      imag = file.files.map((el, i) => {
        console.log()
        return <div key={i} className='hidediv'>
          <Fade {...fadeProperties}>
            <div className="each-fade">
              <div className="image-container">
                <img src={el.path} alt='pic' height='100%' width='100%' />
              </div>

            </div> </Fade>
        </div>
      })}



      return (
        <div>
          <div style={style.main}>
            <div style={style.images}>
              {imag}
            </div>
            <div style={style.detail}>
              <div style={style.priceDetail}>
                <h4>Demand price :Rs{file.price}</h4> <hr />
                <b><h5>{file.purpose}</h5> </b>
               <b> {file.type}</b><br/><hr/>
                {file.Address + ' ' + file.location}
              </div>

              <div style={style.userDetail}>
                <h3>Seller INFO</h3><hr />
                <h4> Abeer Ahmad</h4><br />
                CONTACT: <b>03008501217</b>
              </div>
              <div style={style.addDetails}>
                <b>Description </b><hr />
                Recommendation by the Seller:<br />
                {/* {file.recommendation.map((data,i)=>{

              })} */}
                <b> {data}</b>




              </div>
            </div>
          </div>

        </div>
      )
    }
  }
  const mapStateToProps = (store) => {
    return {
      file: store.Reducer_addetails
    }
  }
  export default connect(mapStateToProps)(Addetail);
